'use client';

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar, Pie } from 'react-chartjs-2';
import React from 'react';

ChartJS.register(CategoryScale, LinearScale, BarElement, ArcElement, Tooltip, Legend);

const voteData = {
  regions: ['North', 'South', 'East', 'West'],
  votesByRegion: [120, 150, 90, 60],
  parties: ['Party A', 'Party B', 'Party C'],
  votesByParty: [180, 140, 100],
};

export default function AdminPage() {
  const regionChartData = {
    labels: voteData.regions,
    datasets: [
      {
        label: 'Votes by Region',
        data: voteData.votesByRegion,
        backgroundColor: ['#f87171', '#60a5fa', '#facc15', '#4ade80'],
      },
    ],
  };

  const partyChartData = {
    labels: voteData.parties,
    datasets: [
      {
        label: 'Votes by Party',
        data: voteData.votesByParty,
        backgroundColor: ['#8b5cf6', '#0ea5e9', '#f97316'],
      },
    ],
  };

  return (
    <main className="min-h-screen bg-white px-6 py-10">
      <h1 className="text-3xl font-bold mb-10">üó≥Ô∏è VoteX Admin Page</h1>

      <section className="mb-16">
        <h2 className="text-2xl font-semibold mb-4">Votes by Region</h2>
        <Bar data={regionChartData} />
      </section>

      <section>
        <h2 className="text-2xl font-semibold mb-4">Votes by Party</h2>
        <Pie data={partyChartData} />
      </section>
    </main>
  );
}
